import { SkillBuilders } from "ask-sdk-core";
import { LambdaHandler } from "ask-sdk-core/dist/skill/factory/BaseSkillFactory";
import { AmazonHelpIntentHandler } from "./handlers/common/AmazonHelpIntentHandler";
import { CustomErrorHandler } from "./handlers/common/CustomErrorHandler";
import { SessionEndedHandler } from "./handlers/common/SessionEndedHandler";
import { AmazonCancelOrStopIntentHandler } from "./handlers/common/AmazonCancelOrStopIntentHandler";
import { LaunchRequestHandler } from "./handlers/common/LaunchRequestHandler";
//import { LocalizationInterceptor } from "./LocalizationInterceptor";
import { YesIntentHandler } from "./handlers/custom/YesIntentHandler";
import { NoIntentHandler } from "./handlers/custom/NoIntentHandler";
import { SmallBusinessLeadFormIntentHandler } from "./handlers/custom/SmallBusinessLeadFormIntentHandler";

function buildLambdaSkill(): LambdaHandler {
    return SkillBuilders.custom()
        .addRequestHandlers(
            new AmazonCancelOrStopIntentHandler(),
            new LaunchRequestHandler(),
            new SessionEndedHandler(),
            new AmazonHelpIntentHandler(),
            new YesIntentHandler(),
            new NoIntentHandler(),
            new SmallBusinessLeadFormIntentHandler()
        )
        .addErrorHandlers(new CustomErrorHandler())
        //.addRequestInterceptors(new LocalizationInterceptor())
        .lambda();
}
// Lambda handler - entry point for skill
export const handler = buildLambdaSkill();
