import { HandlerInput, RequestHandler, getRequestType, getIntentName } from "ask-sdk-core";
import { Response } from "ask-sdk-model";

export class YesIntentHandler implements RequestHandler {
    canHandle(handlerInput: HandlerInput): boolean {
        const requestEnvelope = handlerInput.requestEnvelope;
        return getRequestType(requestEnvelope) === 'IntentRequest' && (getIntentName(requestEnvelope) === 'AMAZON.YesIntent');
    }
    handle(handlerInput: HandlerInput): Response {
        const responseBuilder = handlerInput.responseBuilder;
        const requestAttributes = handlerInput.attributesManager.getRequestAttributes();
        return responseBuilder.speak(requestAttributes.t("PROMPT_EMAIL_ADDRESS"))
            .reprompt(requestAttributes.t("PROMPT_EMAIL_ADDRESS"))
            .withShouldEndSession(false)
            .getResponse();
    }
}