export class SmallBusinessLeadFormIntentHandler implements RequestHandler {
    canHandle(handlerInput: HandlerInput): boolean | Promise<boolean> {
        const requestEnvelope = handlerInput.requestEnvelope;
        return getRequestType(requestEnvelope) === "IntentRequest" && getIntentName(requestEnvelope) === "SmallBusinessLeadFormIntent";
    }
    handle(handlerInput: HandlerInput): Response {
        const responseBuilder = handlerInput.responseBuilder;
        const requestEnvelope = handlerInput.requestEnvelope;
        let request = (requestEnvelope.request) as IntentRequest;
        //return responseBuilder.addDelegateDirective(request.intent).getResponse();

        // Reading property from slot values
        const financingOptionSlot = getSlotValue(requestEnvelope, 'financingOption');
        const firstNameSlot = getSlotValue(requestEnvelope, 'firstName');
        const lastNameSlot = getSlotValue(requestEnvelope, 'lastName');
        const contactNumberSlot = getSlotValue(requestEnvelope, 'contactNumber');
        const emailAddressSlot = getSlotValue(requestEnvelope, 'emailAddress');

        
      //  connectDB();

        let output: string = "";

        // let model = new LeadFormModel({ financingOptionSlot, firstNameSlot, lastNameSlot, contactNumberSlot, emailAddressSlot });
        // model.save((err: any) => {
        //     if (err) {
        //         output = 'There is some problem in saving your information';
        //     } else {
        //         output = 'Thanks for providing the information. One of our business representative will contact you shortly';
        //     }
        // });

        return responseBuilder.speak(output).withShouldEndSession(false).getResponse();
    }

}